* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body{
  height: 100vh;
  background-position: top fixed;
  background: -webkit-linear-gradient(top, lightblue, pink);
  background: linear-gradient(to bottom, lightblue, pink);
  background-repeat:no-repeat;
  background-attachment:fixed;
}
.Header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
}
.Category{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;
}
.buttonQuery{
    margin: 0 5px;
    padding: 5px 10px;
    background-color: pink;
    border: none;
    border-radius: 5px;
    font-family: fantasy;
    font-size: 18px;
    color: #00EFFF;
    cursor: pointer;
    outline: none;
}
.activeBtn{
    margin: 0px 5px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    font-family: fantasy;
    font-size: 18px;
    cursor: pointer;
    background-color: #00EFFF;
    color: pink;
    outline: none;
}
.button:hover{
    background-color: #00EFFF;
    color: pink;
}
.MoviesList{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.MoviesListItem{
    padding: 10px;
    display: flex;
    width: 350px;
    background-color: #00EFFF;
    position: relative;
    border-radius: 10px;
    margin-bottom: 10px;
    background: -webkit-linear-gradient(top, pink, lightblue);
    background: linear-gradient(to bottom, pink, lightblue);
}
.poster{
    height: 250px;
    display: block;
}
.title{
    font-family: fantasy;
    text-align: center;
    margin-bottom: 5px;
    font-size: 16px;
}
.overview{
    height: 88px;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}
.rating{
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: pink;
    border-radius: 10px 0px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
}
.discriptions{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-left: 10px;
}
.link{
    text-decoration: none;
    color: #ff00ff;
}
.link:active{
    text-decoration: none;
    color: #ff00ff;
}
.GenresList{
    display: flex;
    flex-wrap: wrap;
    padding: 0px 30px;
    justify-content: center;
    margin-bottom: 20px;
}
.genreLink{
    text-decoration: none;
    font-size: 25px;
    font-family: fantasy;
    width: 25%;
    text-align: center;
}
.selected{
    text-decoration: none;
    font-size: 25px;
    font-family: fantasy;
    width: 25%;
    text-align: center;
    color: palevioletred;
}
.GenresFilmListItem{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 20%;
    margin-bottom: 20px;
    text-align: center;
}
.genreItemPoster{
    width: 150px;
}
.genresFilmtitle{
    margin-top: 10px;
    font-family: fantasy;
}
.GenresFilmList{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.CurrentMovie{
    display: flex;
    margin-bottom: 20px;
}
.information{
    width: 600px;
}
.posterBlock{
    margin-right: 20px;
}
.titleFilm{
    margin-bottom: 5px;
}
.paragraph{
    margin-bottom: 5px;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace
}
.aboutFilm{
    display: block;
    margin-bottom: 10px;
    font-size: 20px;
    font-family: fantasy;
}
.genre{
    margin-bottom: 10px;
}
.genreItem{
    margin-right: 10px;
    display: inline-block;
    font-family: fantasy;
    font-size: 20px;
}
.genreItem:last-child{
    margin-right: 0;
}
.SimilarMovieItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 20%;
    margin-bottom: 10px;
}
.similarPoster{
    width: 100px;
    margin-bottom: 10px;
}
.similarTitle{
    width: 200px;
    text-align: center;
}
.linkMore{
    width: 100%;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    color: red;
    font-weight: 600;
}
.SimilarLink{
    width: 100%;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
    color: red;
}
.SimilarMovie{
    display: flex;
    flex-direction: column;
    width: 80%;
}
.similarMovie_title{
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
}
.similarMovieList{
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
}
.Movie{
    padding: 0px 30px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Logo{
    display: flex;
    align-items: center;
}
.LogoImage{
    width: 50px;
}
.LogoText{
    text-transform: uppercase;
    font-family: fantasy;
    font-size: 20px;
}
.Menu{
    display: flex;
    color: transparent;
}
.btn{
    margin: 0 5px;
    padding: 5px 10px;
    background-color: pink;
    border: none;
    border-radius: 5px;
    font-family: fantasy;
    font-size: 18px;
    color: #00EFFF;
    cursor: pointer;
    outline: none;
    text-decoration: none;
}

.btnActive{
    margin: 0px 5px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    font-family: fantasy;
    font-size: 18px;
    cursor: pointer;
    background-color: #00EFFF;
    color: pink;
    outline: none;
}
.SearchForm{
    display: flex;
    margin-left: 5px;
}
.inputSearch{
    margin-right: 10px;
    padding-left: 10px;
}
.button{
    padding: 5px 20px;
    background-color: pink;
    border: none;
    border-radius: 5px;
    font-family: fantasy;
    font-size: 18px;
    color: #00EFFF;
    cursor: pointer;
}
